import { useRouter } from "next/router";

interface BookingDetails {
  propertyName: string;
  price: number;
  bookingFee: number;
  totalNights: number;
  startDate: string;
}

const OrderSummaryPage = () => {
  const router = useRouter();
  const bookingDetails = router.query as unknown as BookingDetails;

  if (!bookingDetails.propertyName) {
    return <p className="p-6">No booking details found.</p>;
  }

  return (
    <div className="bg-white p-6 shadow-md rounded-lg max-w-lg mx-auto mt-8">
      <h2 className="text-xl font-semibold">Review Order Details</h2>
      <div className="flex items-center mt-4">
        <img
          src="https://example.com/property.jpg"
          alt="Property"
          className="w-32 h-32 object-cover rounded-md"
        />
        <div className="ml-4">
          <h3 className="text-lg font-semibold">{bookingDetails.propertyName}</h3>
          <p className="text-sm text-gray-500">
            {bookingDetails.startDate} â€¢ {bookingDetails.totalNights} Nights
          </p>
        </div>
      </div>

      <div className="mt-6 space-y-2">
        <div className="flex justify-between">
          <p>Booking Fee</p>
          <p>${bookingDetails.bookingFee}</p>
        </div>
        <div className="flex justify-between">
          <p>Subtotal</p>
          <p>${bookingDetails.price}</p>
        </div>
        <div className="flex justify-between font-semibold">
          <p>Grand Total</p>
          <p>${Number(bookingDetails.bookingFee) + Number(bookingDetails.price)}</p>
        </div>
      </div>
    </div>
  );
};

export default OrderSummaryPage;
